
import styled from 'styled-components';

export const Button = styled.button`
    text-transform: capitalize;
    font-size:1rem;
    background: transparent;
    border: 0.05rem solid var(--lightBlue);
    border-color: ${(props: any) => props.blueButton ? "var(--mainYellow)" : "var(--lightBlue)"};
    color: ${(props: any) => props.blueButton ? "var(--mainYellow)" : "var(--lightBlue)"};
    border-radius: 0.5rem;
    padding: 0.2rem 0.5rem; 
    cursor: pointer;
    margin: 0.2rem 0.5 rem 0.2rem 0;
    transition: all 0.5s ease-in-out;
    &:hover{
        background: ${(props: any) => props.blueButton ? "var(--mainYellow)" : "var(--lightBlue)"};
        color: var(--mainBlue);
    }
    %:focus {
        outline: none;
    }
`;