import React from "react";
import { Button } from "./Button";

interface projectInfo {
  id: number;
  name: string;
  description: string;
  gitUrl: string;
  liveUrl: string;
  screenshot: string;
}

export default function Project(props: projectInfo) {
  return (
    <div className="col-9 mx-auto col-md-6 col-lg-6 my-3">
      <h4 className="text-capitalize">{props.name}</h4>
      <img src={props.screenshot} alt={props.name} className="card-img-top" />
      <div className="card-footer d-flex justify-content-between">
        <Button>Github</Button>
        <Button>Live Link</Button>
      </div>
    </div>
  );
}
